import React, { Fragment } from "react"
import { Link } from "gatsby"
import {publicationInfoList} from "../contents/publication-info";

const PublicationSelection = ({projectTag}) => (
    <div className="content">
        <div className="content has-text-justified">
        {
                publicationInfoList.filter((pub) => {return pub.tags.indexOf(projectTag) >= 0;}).sort((pubA, pubB) => {return pubB.year - pubA.year;}).map((publication, index) => {
                    return <p key={index}>
                        {publication.text}.&nbsp;
                        <Fragment>
                            {
                                publication.links.map((item, indexI) => {
                                    return <Fragment key={indexI}>
                                    {
                                        item.link.indexOf(".pdf") >= 0 ? <Fragment><a href={item.link} target="_blank" rel="noopener noreferrer">{item.label}</a></Fragment> :
                                        <Fragment>[<a href={item.link} target="_blank" rel="noopener noreferrer">{item.label}</a>]</Fragment>
                                    } 
                                    </Fragment>
                                })
                            }
                        </Fragment>
                    </p>
                })
            }

        </div>
    </div>
)

export default PublicationSelection