import React from "react"
import { Link } from "gatsby"

import Layout from "../components/layout"
//import Image from "../components/image"
import SEO from "../components/seo"
import Introduction from "../components/introduction"
import PublicationSelection from "../components/publication-selection"
import ProjectSelection from "../components/project-selection"

const IndexPage = () => (
  <Layout>
    <SEO
      title="Pei-Yao Hung, Human-Computer Interaction Researcher" // use the default
      description="A Human-Computer Interaction (HCI/UX) researcher interested in designing systems to empower people in their everyday pursuits, such as social interaction and health."
      keywords={[
        `Pei-Yao Hung`,
        `researcher`,
        `Human-Computer Interaction`,
        `Computer-Supported Cooperative Work`,
        `Ubiquitous Computing`,
        `End-User Programming`,
        `HCI`,
        `CSCW`,
        `user experience`,
        `UX`,
        `health`,
        `game`,
        `expertise`,
        `collaboration`,
        `patient`,
        `caregiver`,
        `clinician`,
      ]}
    />
    <h1 className="title is-3 is-spaced is-size-4-mobile">About</h1>
    <Introduction></Introduction>
  </Layout>
)

export default IndexPage
