//  <img src={projectInfo.image_info.link} />

const ProjectSelection = ({ description }) => (
    <div className="content has-text-justified">
        {description != undefined ? <Fragment>{description} <hr /></Fragment> : null}
        <div className="columns is-multiline is-4">
                {
                    projectInfoList.map((projectInfo, index) => {
                        return <div key={index} className="column is-half">
                        <Link to={projectInfo.details_link}>
                        <div className="box" style={{width: "100%", height: "100%"}}>
                            <div className="is-size-5  is-size-6-mobile has-text-weight-semibold">
                                {projectInfo.title}
                            </div>
                            <br />
                            <picture>
                                <source type="image/webp" srcset={`${projectInfo.image_info.link.substring(0, projectInfo.image_info.link.lastIndexOf("."))}.webp`}/>
                                <source type="image/jpeg" srcset={projectInfo.image_info.link}/>
                                <img className="image" src={projectInfo.image_info.link} alt=""/>
                            </picture>
                            <br />
                            <div className="tags is-hidden">
                                {
                                    projectInfo.topic_list.map((topic, indexT) => {
                                        return <span className="tag is-medium" key={indexT}>{topic}</span>
                                    })
                                }
                            </div>
                        </div>
                        </Link>
                    </div>
                    })
                }
            </div>
        <hr />
    </div >
)

export default ProjectSelection

